html, body {
    margin: 0;
    height: 100%;
  }
  
  body {
    background-color: #ffffff;
    font-family: Helvetica, Arial, sans-serif;
    text-align: center;
  }
  
  #root {
    position: relative;
    height: 100%;
    width: 100%;
    padding-top: 15px;
  }
  
  #root div {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  
  #root div div {
    height: 100%;
  }
  
  #root div div div {
    position: relative;
    height: auto;
  }
  
  h1 {
    margin-left: 4%;
    margin-right: 4%;
  }
  
  ul {
    list-style-type: none;
    padding: 0;
  }
  
  img {
    max-width: 90%;
    max-height: 390px;
    top: 5%;
    left: 5%;
  }
  
  /* button */
  button {
      -webkit-transition-duration: 0.1s; /* Safari */
      transition-duration: 0.1s;
      background-color: #F4595B;
  
      border-radius: 8px;
      border-bottom: 4px solid #C24648;
      color: white;
      padding: 15px 32px;
      text-align: center;
      text-decoration: none;
      display: inline-block;
      font-size: 16px;
      font-family: 'Oxygen', sans-serif;
      letter-spacing: 2px;
  }
  
  button:hover {
    background-color: #FF7375;
    border: none;
    border-radius: 8px;
    border-bottom: 4px solid #C24648;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    font-family: 'Oxygen', sans-serif;
    letter-spacing: 2px;
  }
  
  button:active {
    background-color: #C24648;
    border: none;
    border-radius: 8px;
    border-bottom: 4px solid #C24648;
    color: #CCC;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    font-family: 'Oxygen', sans-serif;
    letter-spacing: 2px;
  }
  